@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  text-decoration: none;
}
body{
  background-color: rgb(79, 88, 138);
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  text-transform:capitalize;
  border-bottom: 1px solid #f2f2f2;
  letter-spacing:.8px;
}
.navbar h1 {
  color: #ffffff;
  cursor: pointer;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  text-transform:capitalize;
  letter-spacing:.8px;
}
.blog-prev {
  padding: 10px 16px;
  margin: 20px 0;
  border: 1px solid gray;
  border-radius:8px;
  position: relative;
}
.blog-prev:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-prev h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
.blog-prev button{
  color: red;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.blog-details h2 {
  font-size: 20px;
  color: white;
  margin-bottom: 10px;
}

.blog-details p {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}
.blog-details div {
  margin: 20px 0;
  color:white;
}
.blog-details button {
  background: #f1356d;
  color: white;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
  color: white;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}